import React,{useState,useEffect} from 'react'
import { Skeleton,BackTop } from 'antd'
import Logo from '../components/common/logo'
import {Svg1,Svg2,Svg3,Svg4,Arrow,Create,Login,Refs,Income} from '../components/common/svg'
import Web3 from 'web3';
import config from '../config'
import contractAbi from '../assets/abicontract';
const web4 = new Web3(`${config.URL}/${config.API_KEY}`);
const contract = new web4.eth.Contract(contractAbi,config.CONTRACT_ADDRESS);
const prices = [,0.05,0.15,0.45,1.35,4.05,12.15,36.45,109.35];
const WsCom = (props = {ethAmount: 0}) => {
    const [btcP,setBtcP] = useState(null);
    const [usdtP,setUsdtP] = useState(null);
    const {ethAmount} = props;
    useEffect(()=>{
        let socket;
        const wsData = () => {
            socket = new WebSocket('wss://stream.binance.com:9443/stream?streams=ethbtc@aggTrade.b10/ethusdt@aggTrade.b10');
            socket.addEventListener("close",wsData);
            socket.addEventListener("open",()=>{
            })
            socket.addEventListener("message",handleData); 
        }
        wsData();
        return () => {
            socket.close();
        }
    },[null])
    const handleData = (event) => {
        const dt = JSON.parse(event.data);
        switch(dt.stream){
            case "ethusdt@aggTrade":
            setUsdtP(dt.data.p);
            break;
            case "ethbtc@aggTrade":
            setBtcP(dt.data.p);
            break;
        }
    }
    return (
        <div className="">
            <div className="flex justify-between text-lg text-cryp-text mb-2">
                <div className="tetx-gray-700">Amounts in BTC</div>
                <div className="text-gray-700 font-bold">{btcP ? Number(btcP*ethAmount).toFixed(4) : '--'}</div>
            </div>                                    
            <div className="flex justify-between text-lg text-cryp-text mb-2">
                <div className="tetx-gray-700">Amounts in USDT</div>
                <div className="text-gray-700 font-bold">{usdtP ? Number(usdtP*ethAmount).toFixed(4) : '--'}</div>
            </div>                                    
        </div>
    )
}
const TxsCom = () => {
    const [lvsCount,setLvsCount] = useState(null);
    useEffect(()=>{        
        handle();
    },[null])
    const handle = async ()=>{
        try {
            let lvs = [0,["1"],["2"],["3"],["4"],["5"],["6"],["7"],["8"]];
            const curId = await contract.methods.currUserID().call();
            lvs[0] = curId.toString();
            const txs = await contract.getPastEvents("allEvents",{fromBlock: 4996358,topics:["0x1fb338d47f9358d9c3a1929acf6d5786ab53152c2c42fbe7d268004ece84596e"]});
            for (let tx of txs){
                if (lvs[Number(tx.returnValues._level.toString())].indexOf(tx.returnValues._user.toString()) === -1){
                    lvs[Number(tx.returnValues._level.toString())].push(tx.returnValues._user.toString());
                }
            }
            setLvsCount(lvs);
        } catch(err){
            // alert(err);
        }
    }
    return (
        lvsCount ? (
            <div className="rounded shadow px-4 py-4" style={{minHeight: '260px'}}>
                 <div className="text-center text-2xl font-bold text-cryp-hover">Total Members</div>
                <div className="text-center text-2xl text-cryp-text mb-4">{lvsCount[0]}</div>
                <div className="flex flex-wrap justify-between">
                    <div className="w-1/2 text-lg text-gray-700 mb-2">
                        1 lv <span className="font-bold text-gray-700">{lvsCount[0]}</span>
                    </div>
                    <div className="w-1/2 text-gray-700 text-right text-lg mb-2">
                        2 lv <span className="font-bold">{lvsCount[2].length}</span>
                    </div>
                    <div className="w-1/2 text-lg text-gray-700 mb-2">
                        3 lv <span className="font-bold text-gray-700">{lvsCount[3].length}</span>
                    </div>
                    <div className="w-1/2 text-gray-700 text-right text-lg mb-2">
                        4 lv <span className="font-bold">{lvsCount[4].length}</span>
                    </div>
                    <div className="w-1/2 text-lg text-gray-700 mb-2">
                        5 lv <span className="font-bold text-gray-700">{lvsCount[5].length}</span>
                    </div>
                    <div className="w-1/2 text-gray-700 text-right text-lg mb-2">
                        6 lv <span className="font-bold">{lvsCount[6].length}</span>
                    </div>
                    <div className="w-1/2 text-lg text-gray-700 mb-2">
                        7 lv <span className="font-bold text-gray-700">{lvsCount[7].length}</span>
                    </div>
                    <div className="w-1/2 text-gray-700 text-right text-lg mb-2">
                        8 lv <span className="font-bold">{lvsCount[8].length}</span>
                    </div>
                </div>                                
            </div>
        ):(
            <div className="rounded shadow px-4 py-4" style={{minHeight: '260px'}}>
                <Skeleton active paragraph={{rows: 5}} />
            </div>
        )
    )
}
const LoadData = () => {
    const [txsCount,setTxsCount] = useState(null);
    const [allvalues,setValues] = useState(null);
    const [rawTxs,setRawTxs] = useState(null);
    useEffect(()=>{
        const getalltx = async () => {
            console.log('get1')
            const alltx = await contract.getPastEvents("allEvents",{fromBlock: 4996358,topics:["0xce7dc747411ac40191c5335943fcc79d8c2d8c01ca5ae83d9fed160409fa6120"]});
            const time = Date.now() - 24*60*60*1000;
            let tx24h = 0;
            let values = 0;
            for (let tx of alltx){
                values += prices[Number(tx.returnValues._level.toString())];
                console.log(tx)
                if (Number(tx.returnValues._time.toString())*1000 > time){
                    tx24h++;
                }
            }
            setTxsCount([alltx.length,tx24h]);
            setValues([values]);
            setRawTxs(alltx);
        }
        getalltx();
    },[null])
    const renderTime = (time) => {
        const t = new Date(time*1000);
        return `${t.getFullYear()}-${("0" + (t.getMonth()+1)).slice(-2)}-${("0" + t.getDate()).slice(-2)} ${("0"+t.getHours()).slice(-2)}:${("0" + t.getMinutes()).slice(-2)}`;
    }
    return (
        <div className="container px-4 py-32 mx-auto">
            <h2 className="text-4xl text-cryp-text text-center mb-8">Live statistics</h2>
            <div className="flex flex-wrap lg:-mx-4 mb-16">
                <div className="w-full lg:w-1/3 lg:px-4 mb-4">
                    <TxsCom />                            
                </div>
                <div className="w-full lg:w-1/3 lg:px-4 mb-4">
                    <div className="rounded shadow px-4 py-4" style={{minHeight: '260px'}}>
                        {txsCount?(
                            <>
                                <div className="text-center text-2xl font-bold text-cryp-hover">Project Preview</div>
                                <div className="text-center text-2xl text-cryp-text mb-4">-- years</div>
                                <div className="">
                                    <div className="flex justify-between text-lg text-cryp-text mb-2">
                                        <div className="tetx-gray-700">Transactions</div>
                                        <div className="text-gray-700 font-bold">{txsCount[0]}</div>
                                    </div>
                                    <div className="flex justify-between text-lg text-cryp-text">
                                        <div className="tetx-gray-700">Transactions for 24h</div>
                                        <div className="text-gray-700 font-bold">{txsCount[1]}</div>
                                    </div>
                                </div>
                            </>
                        ):<Skeleton active paragraph={{rows: 5}}/>}
                    </div>
                </div>
                <div className="w-full lg:w-1/3 lg:px-4 mb-4">
                    <div className="rounded shadow px-4 py-4" style={{minHeight: '260px'}}>
                        {allvalues?(
                            <>
                                <div className="text-center text-2xl font-bold text-cryp-hover">Value</div>
                                <div className="text-center text-2xl text-cryp-text mb-4">{allvalues[0]} ETH</div>
                                <WsCom ethAmount={allvalues[0]}/>                                        
                            </>
                        ):(<Skeleton active paragraph={{rows: 5}} />)}
                    </div>
                </div>
            </div>
            <h2 className="text-4xl text-cryp-text text-center mb-8">Lastest transactions</h2>
            <div className="w-full">
                {rawTxs ? (
                    <table className="w-full">
                        <thead>
                            <tr>
                                <th className="text-lg w-1/3">Date</th>
                                <th className="text-lg">Lv</th>
                                <th className="text-lg text-center">Amount</th>
                                <th className="text-lg text-right">Tx</th>
                            </tr>
                        </thead>
                        <tbody>
                            {rawTxs.map((val,key)=>{
                                if (key < 10){
                                    return (
                                        <tr key={key}>
                                            <td className="px-1 py-2 text-lg text-gray-600">{renderTime(rawTxs[rawTxs.length-key-1].returnValues._time.toString())}</td>
                                            <td className="px-1 py-2 text-lg text-gray-600">{rawTxs[rawTxs.length-key-1].returnValues._level.toString()}</td>
                                            <td className="px-1 py-2 text-lg text-gray-600 text-center">{prices[rawTxs[rawTxs.length-key-1].returnValues._level.toString()]}</td>
                                            <td className="px-1 py-2 text-lg text-gray-600 text-right"><a href={`https://etherscan.io/tx/${rawTxs[rawTxs.length-key-1].transactionHash}`} className="text-cryp-hover hover:text-cryp-bold" target="_blank">View Tx</a></td>
                                        </tr>
                                    )
                                }                                        
                            })}
                        </tbody>
                    </table>
                ):(
                    <>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                        <div className=""><Skeleton active paragraph={{rows: 2}} title={false}/></div>
                    </>
                )}
            </div>
        </div>
    )
}
